import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: '首页',
    component: HomeView
  },
  {
    path: '/about',
    name: '关于',
    component: () => import('../views/AboutView.vue')
  },
  {
    path: "/overtimeWork",
    name: "加班插件配置",
    meta: {alive: true},
    component: () => import("@/views/OvertimeWorkConfig.vue")
  },
  {
    path: "/OBSView",
    name: "OBS显示界面",
    meta: {obsView: true},
    component: () => import("@/views/OBSView.vue"),
    children: [
      {
        path: "overtimeWork",
        name:"加班显示界面",
        meta: {obsView: true},
        component: () => import("@/components/ViewOvertimeWork")
      }
    ]
  },

]

const router = new VueRouter({
  routes
});

// 每个一级路由被触发之后都会被调用
router.afterEach((to) => {
  console.log(to);
  document.title = "海粟云直播插件 - " + to.name;
});

export default router
