<template>
  <b-container id="app" fluid>
    <b-container class="container" v-if="!$route.meta['obsView']">
      <hr>
      <b-nav align="left" >
        <b-nav-item>
          <router-link to="/">
            <b-btn variant="primary">首页</b-btn>
          </router-link>
        </b-nav-item>
        <b-dropdown
            id="my-nav-dropdown"
            text="组件"
            class="m-md-2 m-xl-2 m-1"
            size="sm"
            variant="primary"
        >
          <b-dropdown-text style="width: max-content">
            点击组件名进入配置界面<br>
            <b-badge>(部分组件不能使用，可能代表将来会更新的组件)</b-badge>
          </b-dropdown-text>
          <b-dropdown-divider/>
          <router-link to="/overtimeWork" tag="b-dropdown-item">加班插件</router-link>
        </b-dropdown>
        <b-nav-item>
          <router-link to="/about">
            <b-btn variant="primary">关于</b-btn>
          </router-link>
        </b-nav-item>
      </b-nav>
    </b-container>
    <keep-alive>
      <router-view v-if="$route.meta['alive']"/>
    </keep-alive>
    <router-view v-if="!$route.meta['alive']"/>
  </b-container>
</template>

<script>

import axios from "axios";

export default {
  name: 'App',
  created() {
    this.$WebSocket.webSocket = new WebSocket(`ws://${location.host}/WSLink`);
  },
  mounted() {
    // 初始化axios超时时间
    axios.defaults.timeout = 3 * 1000;

    // WS管道连接
    this.$WebSocket.webSocket.onerror = () => {
      console.log("WebSocket通道出现错误。将自动关闭通道。");
      this.$WebSocket.webSocket.close();
    }
    // WS管道关闭
    this.$WebSocket.webSocket.onclose = () => {
      console.log("WebSocket通道已经关闭。");
    }
    // WS管道消息捕获
    this.$WebSocket.webSocket.onmessage = MessageEvent => {
      let message = JSON.parse(MessageEvent.data);
      // 信息捕获开始
      if (message.type === "StreamStart") return this.$store.commit("setLiveStatus",true);
      // 信息捕获结束
      if (message.type === "StreamEnd") return this.$store.commit("setLiveStatus",false);
      // 信息捕获早已开始
      if (message.type === "liveStreamStatus") return this.$store.commit("setLiveStatus",message['status']);
      // 其他类型的信息转发到Vue事件总线
      this.$EventBus.$emit(message.type ? message.type : "message", MessageEvent.data);
    }
  }
}
</script>

