<template>
  <b-container fluid id="content">
    <hr>
    <div style="background-color: rgba(255,255,255,0.3)">
      <h1>海粟云 B站直播 显示插件</h1>
      <b-overlay :show="overlayStatus" rounded="sm">
        <b-container>
          <hr>

          <b-row class="justify-content-md-center">
            <b-col>
              <b-btn @click="sendTest">测试连接</b-btn>
            </b-col>
            <b-col>
              <b-btn @click="toggleLiveStatus">切换信息流捕获</b-btn>
            </b-col>
            <b-col>
              <b-btn @click="coreHandle('restart')">重启插件核心</b-btn>
            </b-col>
            <b-col>
              <b-btn @click="coreHandle('exit')">退出插件核心</b-btn>
            </b-col>
          </b-row>

          <hr>
          <!--直播间链接-->
          <b-row class="justify-content-md-center mainData">
            <b-col cols="3"><span>直播间地址</span></b-col>
            <b-col cols="7">
              <b-form-input disabled :value="liveRoomURL"/>
            </b-col>
            <b-col cols="2">
              <b-btn @click="copyClick('liveRoomAddress')">复制</b-btn>&nbsp;
              <b-btn @click="openNewTabs">打开</b-btn>
            </b-col>
          </b-row>

          <!--直播间信息流状态-->
          <b-row class="justify-content-md-center mainData">
            <b-col cols="3"><span>信息流状态</span></b-col>
            <b-col cols="7">
              <b-form-input disabled :value="getLiveStatus"/>
            </b-col>
            <b-col cols="2">
              <b-btn @click="copyClick('liveStatus')">复制</b-btn>
            </b-col>
          </b-row>

          <!--心跳包发送间隔-->
          <b-row class="justify-content-md-center mainData">
            <b-col cols="3"><span>心跳包间隔</span></b-col>
            <b-col cols="7">
              <b-form-input disabled :value="getHeartBeatDelay"/>
            </b-col>
            <b-col cols="2">
              <b-btn @click="copyClick('HeartBeatDelay')">复制</b-btn>
            </b-col>
          </b-row>

          <!--心跳包内容-->
          <b-row class="justify-content-md-center mainData">
            <b-col cols="3"><span>心跳包内容</span></b-col>
            <b-col cols="7">
              <b-form-input disabled :value="getHeartBeatContent"/>
            </b-col>
            <b-col cols="2">
              <b-btn @click="copyClick('HeartBeatContent')">复制</b-btn>
            </b-col>
          </b-row>
        </b-container>
      </b-overlay>
    </div>
  </b-container>
</template>

<script>
import axios from "axios";
import clipboard from "clipboard";
import {showToast} from "@/common/ToastShow";


export default {
  name: 'MainPage',
  data() {
    return {
      baseInfo: {
        BiliRoomID: null,
        HeartBeatDelaySecond: null,
        HeartBeatContent: null,
        liveInformation: {status: false}
      },
      overlayStatus: false
    }
  },
  methods: {
    async toggleLiveStatus() {
      let result = await this.sendReq("/API/statusToggle");
      if (result.err) return;
      let message = `${result.data['message']} \n当前状态是：${result.data['liveStatus']}`;
      showToast.call(this, "发送成功", message, "success");
      this.baseInfo.liveInformation.status = result.data['liveStatus'];
      this.$forceUpdate();
    },
    async sendTest() {
      let result = await this.sendReq("/test:payload?query=key");
      if (result.err) return;
      showToast.call(this, "请求成功，测试通过", JSON.stringify(result['data']), "success");
    },
    async getCoreInfo() {
      let result = await this.sendReq("/API/coreInfo");
      if (result.err) return;
      this.baseInfo = {...result.data};
    },
    coreHandle(type = "restart") {
      if (type === "restart") {
        this.sendReq("/restartCore").then();
      } else if (type === "exit") {
        this.sendReq("/exitCore").then();
      }
      this.overlayStatus = true;
      setTimeout(() => {
        location.reload();
      }, 1000);
    },
    copyClick(target) {
      let messageNeed2Copy = "";
      switch (target) {
        case "liveRoomAddress":
          messageNeed2Copy = this.liveRoomURL;
          break;
        case "liveStatus":
          messageNeed2Copy = this.$store.state.liveStatus ? "信息流捕获进行中····" : "信息流捕获暂停";
          break;
        case "HeartBeatDelay":
          messageNeed2Copy = this.getHeartBeatDelay;
          break;
        case "HeartBeatContent":
          messageNeed2Copy = JSON.stringify(this.getHeartBeatContent);
          break;
        default:
          messageNeed2Copy = "Null";

      }
      clipboard.copy(messageNeed2Copy);
      showToast.call(this, "完成复制", messageNeed2Copy, "success");
    },
    openNewTabs() {
      open(this.liveRoomURL);
    },
    sendReq(urlString, method = "get") {
      return axios({
        url: urlString,
        method: method
      }).then(axiosRes => {
        return {err: false, data: axiosRes.data};
      }).catch(axiosErr => {
        showToast.call(this, "请求错误", axiosErr.message, "danger");
        return {err: true, object: axiosErr};
      })
    }
  },
  async mounted() {
    this.getCoreInfo().then();
  },
  computed: {
    liveRoomURL() {
      return this.baseInfo.BiliRoomID ? `https://live.bilibili.com/${this.baseInfo.BiliRoomID}` : "Null";
    },
    getHeartBeatDelay() {
      return this.baseInfo.HeartBeatDelaySecond ? `${this.baseInfo.HeartBeatDelaySecond} 秒` : "Null";
    },
    getHeartBeatContent() {
      return this.baseInfo.HeartBeatContent ? this.baseInfo.HeartBeatContent : "Null";
    },
    getLiveStatus() {
      return this.$store.state.liveStatus ? "捕获中···" : "未在捕获中···";
    }
  }
}
</script>

